                  <h4 class=\"tc-card__title text-center mb-3\">Добро пожаловать в Travel<span class=\"tc-text-primary\">Club</span></h4>\n<!--                                <div class=\"card-body\">-->\n<!--                                    <div class=\"row m-auto justify-content-center align-items-center\" style=\"width: 100%; height: 100%;\">-->\n<!--                                        <div class=\"col-12 col-md-12 col-sm-12 p-0\">-->\n<!--&lt;!&ndash;                                            <div class=\"row align-items-center justify-content-center mx-auto mb-3 travel-card-title-row\" style=\"height:100px;width:100%;\">&ndash;&gt;-->\n<!--&lt;!&ndash;                                                <div class=\"col-md-10 p-0\">&ndash;&gt;-->\n<!--&lt;!&ndash;                                                    <div class=\"row me-auto ms-0 my-auto travel-card-title\" style=\"position:relative;height:50px;width:250px;\">&ndash;&gt;-->\n<!--&lt;!&ndash;                                                        <h1 class=\"title-1\"><span>Д</span>обро пожаловать</h1>&ndash;&gt;-->\n<!--&lt;!&ndash;                                                        <img src=\"/images/blue-brush.svg\" class=\"blue-brush\" style=\"position: absolute;\"/>&ndash;&gt;-->\n<!--&lt;!&ndash;                                                    </div>&ndash;&gt;-->\n<!--&lt;!&ndash;                                                </div>&ndash;&gt;-->\n<!--&lt;!&ndash;                                                <div class=\"col-md-7 p-0\">&ndash;&gt;-->\n<!--&lt;!&ndash;                                                    <div class=\"row ms-auto me-0 my-auto travel-card-title\" style=\"position:relative;height:50px;width:280px;\">&ndash;&gt;-->\n<!--&lt;!&ndash;                                                        <h1 class=\"title-2\">в <span>TravelClub Family</span></h1>&ndash;&gt;-->\n<!--&lt;!&ndash;                                                        <img src=\"/images/orange-brush.svg\" class=\"orange-brush\" style=\"position: absolute;\"/>&ndash;&gt;-->\n<!--&lt;!&ndash;                                                    </div>&ndash;&gt;-->\n<!--&lt;!&ndash;                                                </div>&ndash;&gt;-->\n<!--&lt;!&ndash;                                            </div>&ndash;&gt;-->\n<!--                                        </div>-->\n<!--                                    </div>-->\n<!--                                    <div class=\"form-signin\">-->\n                                        <ValidationObserver v-slot=\"{ invalid }\" tag=\"div\" class=\"tc-wrapper-column tc-gap-medium\">\n                                            <base-input\n                                                v-model=\"email\"\n                                                name=\"email\"\n                                                :rules=\"'required|email'\"\n                                                label=\"Email\"\n                                                :disabled=\"loading\"\n                                                no_group_item\n                                            />\n                                            <base-input\n                                                v-model=\"password\"\n                                                name=\"password\"\n                                                :type=\"passwordFieldType\"\n                                                label=\"Пароль\"\n                                                :disabled=\"loading\"\n                                                @click-group-item=\"switchVisibility\"\n                                                :rules=\"'required|min:8'\"\n                                                group_item_action\n                                            >\n                                                <template #icon>\n                                                    <base-icon :name=\"passwordFieldType === 'password' ? 'Eye':'EyeSlash'\"\n                                                               color=\"secondary\"></base-icon>\n                                                </template>\n                                            </base-input>\n\n                                            <button class=\"tc-button text-uppercase mt-3 mx-auto mb-2\" :disabled=\"invalid||loading\" @click=\"signin\">\n                                                    <span v-if=\"loading\" role=\"status\" aria-hidden=\"true\" class=\"spinner-border spinner-border-sm mx-2\"></span>\n                                                    Войти\n                                            </button>\n                                            <p class=\"text-center m-auto\">Нет аккаунта? <router-link to=\"/signup\" style=\"color: #f8a105\">Зарегистрироваться</router-link> </p>\n                                            <p class=\"text-center m-auto\">Забыли пароль? <router-link to=\"/reset-password\" style=\"color: #f8a105\">Сменить пароль</router-link> </p>\n                                        </ValidationObserver>\n<!--                                    </div>-->\n<!--                                </div>-->\n                            </div>\n                        </div>\n                    </div>\n<!--                </div>-->\n<!--            </div>-->\n<!--        </div>-->\n    </div>\n</template>\n\n<script>\n    export default {\n        name: \"SignIn\",\n        data: () => ({\n            email:'',\n            password:'',\n            wrongCredentials:'',\n            passwordFieldType: 'password',\n            loading: false\n        }),\n        computed: {\n            isLoggedIn: function () {\n                return this.$store.getters.isLoggedIn\n            },\n        },\n        mounted() {\n            if(this.isLoggedIn)\n            {\n                this.$router.push('/dashboard')\n            }\n        },\n        methods: {\n            switchVisibility() {\n                this.passwordFieldType = this.passwordFieldType === 'password' ? 'text' : 'password'\n            },\n            async signin() {\n                this.loading = true;\n                let data = {\n                    email: this.email,\n                    password: this.password,\n                };\n                await this.$store.dispatch('login', data)\n                    .then(resp => {\n                        this.loading = false;\n                        this.$router.push('/dashboard')\n                    })\n                    .catch(err => {\n                        this.loading = false;\n                    })\n\n            },\n            sendMessage(message) {\n                this.$notify({\n                    group: 'info',\n                    type: 'error',\n                    title: 'Сообщение от TravelClub',\n                    text: message\n                });\n            },\n        }\n    }\n</script>\n\n<style scoped>\n    .btn-visibility{\n        position: absolute;\n        background: transparent;\n        border: none;\n        outline: none!important;\n        color: #063065;\n        font-size: 20px;\n        right: 10px;\n    }\n    .card-signin {\n        border: 0;\n        border-radius: 1rem;\n        box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);\n    }\n\n    .card-signin .card-title {\n        margin-bottom: 2rem;\n        font-weight: 300;\n        font-size: 1.5rem;\n    }\n\n    .card-signin .card-body {\n        padding: 2rem;\n    }\n\n    .form-signin {\n        width: 100%;\n    }\n\n    .form-signin .btn {\n        font-size: 80%;\n        border-radius: 5rem;\n        letter-spacing: .1rem;\n        font-weight: bold;\n        padding: 1rem;\n        transition: all 0.2s;\n    }\n\n    .form-label-group {\n        position: relative;\n        margin-bottom: 1rem;\n    }\n\n    .form-label-group input {\n        height: auto;\n        border-radius: 2rem;\n    }\n\n    .form-label-group>input,\n    .form-label-group>label {\n        padding: var(--input-padding-y) var(--input-padding-x);\n    }\n\n    .form-label-group>label {\n        position: absolute;\n        top: 0;\n        left: 0;\n        display: block;\n        width: 100%;\n        margin-bottom: 0;\n        /* Override default `<label>` margin */\n        line-height: 1.5;\n        color: #495057;\n        border: 1px solid transparent;\n        border-radius: .25rem;\n        transition: all .1s ease-in-out;\n    }\n\n    .form-label-group input::-webkit-input-placeholder {\n        color: transparent;\n    }\n\n    .form-label-group input:-ms-input-placeholder {\n        color: transparent;\n    }\n\n    .form-label-group input::-ms-input-placeholder {\n        color: transparent;\n    }\n\n    .form-label-group input::-moz-placeholder {\n        color: transparent;\n    }\n\n    .form-label-group input::placeholder {\n        color: transparent;\n    }\n\n    .form-label-group input:not(:placeholder-shown) {\n        padding-top: calc(var(--input-padding-y) + var(--input-padding-y) * (2 / 3));\n        padding-bottom: calc(var(--input-padding-y) / 3);\n    }\n\n    .form-label-group input:not(:placeholder-shown)~label {\n        padding-top: calc(var(--input-padding-y) / 3);\n        padding-bottom: calc(var(--input-padding-y) / 3);\n        font-size: 12px;\n        color: #777;\n    }\n\n\n    /* Fallback for Edge\n    -------------------------------------------------- */\n\n    @supports (-ms-ime-align: auto) {\n        .form-label-group>label {\n            display: none;\n        }\n        .form-label-group input::-ms-input-placeholder {\n            color: #777;\n        }\n    }\n\n    /* Fallback for IE\n    -------------------------------------------------- */\n\n    @media all and (-ms-high-contrast: none),\n    (-ms-high-contrast: active) {\n        .form-label-group>label {\n            display: none;\n        }\n        .form-label-group input:-ms-input-placeholder {\n            color: #777;\n        }\n    }\n\n    .title-1 {\n        position: absolute;\n        z-index: 1;\n        left: 0px;\n        top: 10px;\n        color:#062348;\n        font-family: 'Bello Pro' !important;\n        font-size: 30px;\n    }\n    .title-1 span {\n        font-size: 40px;\n    }\n    .title-2 {\n        position: absolute;\n        z-index: 1;\n        left: 42px;\n        top: 22px;\n        color: white;\n        font-family: 'Open Sans' !important;\n        font-weight: 900;\n        font-size: 20px;\n    }\n    .title-2 span {\n        text-transform: uppercase;\n    }\n    .orange-brush {\n        top: 2px;\n        left: 10px;\n        width: 283px;\n        height: 60px;\n    }\n    .blue-brush{\n        top: 10px;\n        left: -55px;\n        width: 330px;\n        height: 50px;\n        opacity: 1;\n    }\n    .multiselect-title {\n        color: #062348 !important;\n        font-family: \"Bello Pro\";\n        /*font-weight: 700;*/\n    }\n    .multiselect__tags {\n        border: 1px solid #f08b23;\n        border-radius: 20px;\n        font-size: 1rem;\n        min-height: 40px;\n    }\n    .multiselect__tags .travel-text-control{\n        color: #062348;\n        font-family: \"Open Sans\" !important;\n        font-weight: 900 !important;\n    }\n\n    .multiselect__tags .travel-text-control:focus{\n        outline: none!important;\n        box-shadow: none !important;\n    }\n    .validate-error{\n        display: block !important;\n        height: 17px;\n    }\n    .btn {\n        background: #0f213d;\n        color: white;\n        text-transform: uppercase;\n        font-size: 24px;\n        font-family: \"Open Sans\";\n        font-weight: 900;\n        border: 0;\n        border-radius: 50px;\n        outline: none;\n    }\n    .btn:focus {\n        outline: none !important;\n        box-shadow: none !important;\n    }\n    .btn-travel {\n        border-radius: 50px;\n        background: #f08b23;\n        color: white;\n        text-transform: uppercase;\n        font-family: \"Open Sans\";\n        font-weight: 900;\n        font-size: 20px;\n    }\n</style>\n"],"sourceRoot":""}]);
